@import "vars";
@import "fonts";

@import "./blocks/header.scss";
@import "./blocks/menu.scss";
@import "./blocks/about.scss";
@import "./blocks/price.scss";
@import "./blocks/testimonial.scss";
@import "./blocks/order.scss";
@import "./blocks/footer.scss";

html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

body,
h1,
h2,
h3,
p,
ul,
li {
  margin: 0;
  padding: 0;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
  color: inherit;
}

.body {
  position: relative;
  background: $colorRose;
  font-family: "Blackest Text Trial", sans-serif;
  color: $colorWhite;

  &--with-menu {
    overflow: hidden;
  }
}

a,
button {
  &:active {
    -webkit-tap-highlight-color: transparent;
    // -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    -moz-tap-highlight-color: transparent;
    -ms-tap-highlight-color: transparent;
  }
}
